      Glossary
    </div>
    <div class="center" style="font-size: 1.5rem">
      EIP-1559 (Ethereum lmprovement Protocol 1559);
    </div>
    <div class="middle">
      This proposal was initially created by Vitalik Buterin with the intent of
      reducing the cost per transaction by not paying the miners the gas fee that Ethereum users pay by bidding for the gasfee.Ethereum users will now have a more fairly accurate estimate of the average gas price of atransaction based on the network's
      internal averages.A side effect of a more predictable base fee may lead to some reduction in gas prices if we assume
      that fee predictability means users will overpayfor gas less frequently. For more information about how EIP-1559 will change
      Ethereum, see here.
    </div>
    <div class="center" style="font-size:1.5rem;">
      Gas fee:
    </div>
    <div class="middle">
      Gas refers to the transaction fee on theEthereum blockchain. It is what users pay to get theirtransaction valdated, orcompleted.
    </div>
    <div class="middle">
      MetaMask-specific Gas Estimations:
    </div>
    <div class="middle">
      <div>
        <div><li>“Low”: <img :src="i1" />is much lower than market prices and it allows a user to pay a lower fee when they are willing to wait a longer</li></div>
        <div><li>“Market”: <img :src="i2" />reflects market prices.</li></div>
        <div><li>“Aggressive”:<img :src="i3" /> is much higher compared to market prices.It allows you to set a really high maxfee and priority fee toincrease the likelihood of yourtransaction being successful if you're expecting to participate in a gas war.</li></div>
      </div>
    </div>
    <div class="center" style="font-size:1.5rem;">
      Base fee:
    </div>
    <div class="middle">
      Generated by the protocol.Represents the minimum gasUsed multiplier required for a transaction to be included in a block
    </div>
    <div class="middle">
      (i.e.for a transaction to be completed).This is the part of the transaction fee that is burnt.
    </div>
    <div class="center" style="font-size:1.5rem;">
      Max Priority fee - i.e.Priority fee or the miner's tip
    </div>
    <div class="middle">MetaMask wil initially set this amount based on the previous block's history.However,users will be allowed to to edit this amount within the Advanced Settings.It is added to the transaction and represents the part of the transaction fee that goes to the miner.</div>
    <div class="center" style="font-size:1.5rem;">
      Max fee:
    </div>
    <div class="middle">
      MetaMask will initially set this amount based on the previous blocK's history. However, users will be allowed to edit this amount within  the Advanced Settings. It represents the maximum amount that a user is willing to pay for their transaction(inclusive of base fee and   max priority fee).The difference between max fee per gas and base fee + max priority fee is “refunded" to the user.
    </div>
    <div class="center" style="font-size:1.5rem;">
      Gas limit:
    </div>
    <div class="middle">
      The maximum amount of gas units that the transaction may be able to consume.
    </div>
    <div class="center" style="font-size:1.5rem;">
      Gwei:
    </div>
    <div class="middle">
      Gwei is a unit of ether, the smallest denomination, which stands for gigawei (or 1,000,000,00o)' Gweis used for gas fees, or rather payments made by users to compensate forthe computing energy required to process and validate transactions on the Ethereum blockchain.2
    </div>
    <div class="center" style="font-size:1.5rem;">
      Slippage:
    </div>
    <div class="middle">
     Slippage is the expected percentage difference between a quoted and an executed price.
    </div>

