      arr: [
        {
          id: 1,
          title: "What is EIP-1559?",
          con: "EIP-1559 has changed Ethereum's market mechanisms to pay for transaction fees.EIP-1559 got rid of the first-price auction and replacedit with a fixed-price sale.The significance of this change is that peoplesubmitting transactions won't have to guess as much about how muchgas is required, as there is a 'base fee'to be included in the next block.For users or applications that want to prioritize their transaction, theycan add a “tip”(priority fee) to pay a miner. You can read about EIP-1559and its changes to Ethereum in more detail here.",
        },
        {
          id: 2,
          title: "Will EIP-1559 make ETH deflationary?",
          con: "EIP-1559 burns the ETH spent as base fee of the transaction fee.ThatETH is removed from the supply. Under EIP-1559,ETH becomes morescarce, as all transactions on Ethereum burn some amount of ETH. <br><br>Modeling exactly how deflationary EIP-1559 is difficult since you have toproject variables like expected transactions, and, even harder to predict, expected network congestion.In theory, the more transactionsthat occur, the more deflationary pressure that the burning of the basefee will have on the overall Ethereum supply.The ETH supply may deflate more or inflate more at different times based upon the numberof transactions that happen on the network. Since the update to EIP-1559 in August 2021, we have seen deflationary days on the Ethereumblockchain. <br><br>After the merge to Proof of Stake,Justin Drake's model estimates as a”best guess“that 1,000ETH will be issued per day, and 6,000 ETH wouldbe burned.Assuming more validators join and the staking APR is 6.7%，the annual supply change will be -1.6million ETH, reducing the annualsupply rate by 1.4%. <br><br>Despite growing awareness of MEV and potential ElPs to bring moretransparency, we can expect arbitrage opportunities to only get moresophisticated as institutional financial traders use DeFi protocols.Thiscould mean that there may end up being way more spent on tips perblock than the base fee.Zhu Su and Hasu actually predict that less thanhalf of today’s fees could be burned by EIP 1559.",
        },
        {
          id: 3,
          title: "How much ETH has becn burncd?",
          con: "You can track that here.",
        },
        {
          id: 4,
          title: "What has changcs for mc as a user with EIP-1559?",
          con: "The idea of EIP-1559 is to make gas fees more or less transparent for theuser.Therefore, wallets will be able to have better estimates and maketransaction fees more predictable.They won't have to rely much onexternal oracles since the base fee is managed by the protocol itself.There will be additional user experience benefits like automating thefee bidding mechanism,thus reducing delays in transaction confirmation.",
        },
        {
          id: 5,
          title: "What is the difference in gas fees on L1vs L2?",
          con: "To understand why gas cost is different on Layer 1 compared to Layer 2on Ethereum, we need to understand the difference between Layer 1and Layer 2 first. <br><br>Layer 1 is the original Ethereum blockchain which, at the time of writing,is a proof of work (POw) blockchain. Layer 1 has a limited number oftransactions per second it can process.When it's busy on the chain, inother words, a lot of people are making transactions at the same time,the cost of these transactions go up.This can lead to transaction costsof sometimes over hundreds of dollars.This simply isn't worthwhileunless you’re a whale, or just plain desperate to make that transaction.Ethereum 2.0 aims to solve this problem with Proof of Stake, which allows for faster transactions amongst other things. While we wait forEthereum to move to Ethereum 2.0(planned for 2022), there are solutions that solve the problem of high transaction costs: Layer 2. <br><br>Layer 2 is defined as “a collective term for solutions designed to helpscale your application by handling transactions off the EthereumMainnet (layer 1) while taking advantage of the robust decentralizedsecurity model of Mainnet”.They are networks that are built “on top” ofthe Ethereum blockchain that can handle transaction fees off of theoriginal Ethereum blockchain.This alleviates the number of transactionsper second made on the original Ethereum chain, or Layer 1, which allows to scale and therefore decrease the transaction, or gas costs. <br><br>We believe Layer 1 will continue to act as a valuable base issuance andsettlement layer for protocol assets. Whereas, on Layer 2, developersand end-users will want to take advantage of the off-chain transactionsand computations to keep the base layer free of congestion. That means, because of Layer 2’s unique optimizations and scaling solutions,transaction processing speeds will be faster while reducing gas fees willbe lower than Layer 1.",
        },
      ],
      arr2: [
        {
          id: 1,
          title: "Will EIP-1559 make gas fees cheaper?",
          con: "No, this is not the intent of the EIP.As a side effect of a more predictablebase fee,EIP-1559 may lead to some reduction in gas prices if we assume that fee predictability means users will overpay for gas lessfrequently. With EIP-1559,the base fee will increase and decrease by12.5% after blocks are more than 50% full.For example, if a block is100% full the base fee increases by 12.5%; if it is 50% full the base feewill be the same; if it is 0% full the base fee would decrease by 12.5%. <br><br>The ongoing movement of applications to rollups and Layer 2s willgreatly reduce gas fees.",
        },
        {
          id: 2,
          title: "How do l set the right gas fec?",
          con: "MetaMask will provide you with three options when setting the gas fee: <br><br>“Low”(previously“Low”): is much lower than market prices and itallows a user to pay a lower fee when they are willing to wait a longertime, for example when withdrawing funds or other non-time sensitive transactions.lt allows you to wait a longer period and skipthe price spikes (i.e.save money).This setting is based on past trends, which means we are less sure the transaction goes through. lfyou require a transaction to go through within a specific time frame,this may not be the right setting for you.<br><br>“Market”(previously “Medium”): reflects market prices.<br><br>“Aggressive”(previously“High”): is much higher compared to marketprices. It allows you to set a really high max fee and priority fee toincrease the likelihood of your transaction being successful if you'reexpecting to participate in a gas war.",
        },
        {
          id: 3,
          title: "Should ledit the gas fee?",
          con: "You will be able to choose between a low, market or aggressive gas fee.See the question above for what these mean.Additionally, you can edityour gas limit, priority fee and max fee in the “Advanced Settings”.Thiswill override the default settings of low, market or aggressive that wereapplied by MetaMask or the application you are interacting with.",
        },
        {
          id: 4,
          title: "How much faster will my transaction go through on EIP-1559?",
          con: "This remains unknown for now.",
        },
        {
          id: 5,
          title: "How is the gas fee calculated?",
          con: "Gas fees are determined by how full the block is.EIP-1559 establishesthe market rate for transaction inclusion and allows Ethereum to havedouble the blockspace (by increasing max gas limit per block from12.5M to 25M).lt then targets blocks to only be 50% full.<br><br> when the network is >50% utilization, the base fee is increasedwhen <br>Wthe network is <50% utilization, the base fee is decreased <br><br>Over time, Ethereum's block size will average out to be the same exactsize, but this extra capacity allows for flexibility with transaction inclusion. In essence, it will eliminate the extra step in setting the gasprice to make a transaction.",
        },
        {
          id: 6,
          title: "Are gas fees higher on MetaMask than anywhere else?",
          con: "For the “Market” setting, we do our best to find the right balancebetween cost and confidence/speed.Gas costs are an estimate andwhat you actually pay should be similar across wallets. Using the” “market” setting is what we'd expect to be similar when comparing toother wallets. <br><br>“Low” and “Aggressive” will vary much more compared to other walletsbased on how these settings work.",
        },
        {
          id: 7,
          title: "How much will the estimated gas fee differ from what l will actually+pay?",
          con: "The estimated gas fee is a range.The upper bound of this range is themaximum the user will be paying for a transaction. We are analyzingdata to get a better understanding of the difference between theestimated gas fee and the actual gas fee.",
        },
        {
          id: 8,
          title: "Should l consider a different approach for setting gas fces for swas+vs other transactions?",
          con: "MetaMask will have three options to set a gas fee: low, market andaggressive (more details in FAQ above“How do l set the right gas fee?”“and will preselect the fee we think is relevant to your transaction type.You can change this if you prefer, but we provide the best recommendation.For time-sensitive transactions such as Swaps, werecommend and will pre-select an“aggressive” gas fee.",
        },
        {
          id: 9,
          title: "What does base fee/max fee/priority fee/etc.mean?",
          con: "Refer to our Glossary for detailed definitions about(new) terms.",
