        {
          id: 10,
          title: "Since l am setting a ”max priority fce,“ how can lsce the amount that?l actually pay for a transaction?",
          con: "You can view the transaction activity for the estimated gas fee“paid, orview the “effectiveGasPrice”in the transaction receipt on block explorers like Etherscan.",
        },
        {
          id: 11,
          title: "Can I transact without a priority fee(“miner's tip”)?",
          con: "Yes you can, but there is the possibility that other transactions (that doinclude a priority fee) will be prioritized as miners are incentivized toinclude transactions with a priority fee.",
