        {
          id: 12,
          title: "will typical users be selecting the“tip“ amount or will that be part of an overall fee presclected for users?",
          con: "The “tip”, or priority fee, will be included as the overall “gas fee” thatusers see to submit a transaction.You will also be able to“edit gas fee”to increase or decrease this“tip”.",
