          title: "Where can someone see what they paid for a tip/priority fee? will block explorers like Etherscan now show this information?",
          con: "Most of the tooling will be updated accordingly to show the new information related to the EIP.There will now be an“effectiveGasPrice”in the transaction receipt, which returns the price paid post-executionby the transaction (i.e.base fee + priority fee).",
