        },
        {
          id: 2,
          title: "ls the user experience going to be different during times of network+congestion?",
          con: "During periods of high network congestion, the base fee will adjust by12.5% depending how much demand surpasses the ideal gas limit perblock until that demand abates.Instead of a first-price auction, userswill have a better sense of how congested the network is by how highthe base fee is. If it is too congested,the user can either pay that priceor not. Or, they submit a lower fee and wait for the price to go down inthe future.",
        },
      ],
      arr5: [
        {
          id: 1,
          title: "What changes for me asa developer with EIP-1559?",
          con: "we recommend both dapp developers and networks to switch to EIP-1559 fields and block headers respectively if they haven't already. lf not,the legacy gasPrice will be used as maxFeePerGas, which means thatthe user will potentially overpay for their transaction.",
