        {
          id: 2,
          title: "How do l implement EIP-1559 with MetaMask for my dapp?",
          con: "You can read through the EIP-1559 specifications and also look throughthe EIP-1559 sections from Ethereum's JSON-RPC specs.",
        },
        {
          id: 3,
          title: "Is the implementation on a user or dapp level? ln other words, if limplement EIP-1559, will all my dapp users see the new gas fee settings,or only the users who have agreed to adopt EIP-1559?",
          con: "lt's both.The user interfaces with the Ethereum network through a client(wallet). Users can also interface with the network through a dapp.Inthe case that the dapp hasn't switched over to the new EIP-1559 fields,MetaMask will detect this and show pre-1559 gas estimation Uls.",
        },
        {
          id: 4,
          title: "Who estimates gas estimates, the dapp or MetaMask?",
          con: "Gas estimates are done by MetaMask.",
        },
        {
          id: 5,
          title: "Are there any potential risks for dapps on Ethereum?",
          con: "All change is risky, but the Ethereum community has a track record ofstrong software development and coordination.There are some potential risks EIP-1559 presents to network actors that are sensitive totiming, such as oracles.Oracles usually provide the pricing informationneeded to support various actors in the DeFi ecosystem.For example,Compound needs to know the valuation (i.e.price x number of assets) ofa user's collateral in order to determine if their position needs to beliquidated or not.The valuation of these assets have to be constantlyupdated, and Compound relies on oracles to update this information.More information about this can be found in this article.",
