          con: "The technical changes for EIP-1559 (and EIP-3198) that may impact yourproject include a new block format, a new transaction format, the newvalues for selecting a gas price, a new JSON RPC call and changedbehaviour for several JSON RPC calls.For details about these changes,refer to this lnfura blog post.",
